import React, { Component } from "react";
import "./TodoApp.css";

class TodoApp extends Component {
  state = {
    input: "",
    items: [],
  };

  handlerFunction = (event) => {
    this.setState({
      input: event.target.value,
    });
  };

  createTodo = (event) => {
    event.preventDefault();
    console.log(this.state.input);
    this.setState({
      items: [
        ...this.state.items,
        { id: Date.now(), name: this.state.input, status: false },
      ],
      input: "",
    });
    console.log(this.state.items);
  };

  delete = (key) => {
    this.setState({
      items: this.state.items.filter((data, index) => index !== key),
    });
  };

  checked = (key) => {
    this.setState({
      items: this.state.items.filter((data, index) => {
        if (data.id === key) {
          if (data.status) {
            data.status = false;
          } else {
            data.status = true;
          }
        }
        console.log(data);
        return data;
      }),
    });
  };

  render() {
    const { input } = this.state;
    return (
      <div className="todo-container">
        <div className="input-section">
          <h1>Todo App</h1>

          <form action="" onSubmit={this.createTodo}>
            <input
              type="text"
              value={input}
              placeholder="Enter items....."
              onChange={this.handlerFunction}
            />
          </form>
        </div>

        <ul>
          {this.state.items.map((data, index) => (
            <li key={index}>
              <input type="checkbox" onChange={() => this.checked(data.id)} />
              {data.status ? (
                <del>{data.name}</del>
              ) : (
                <span> {data.name}</span>
              )}{" "}
              <i
                className="fas fa-trash-alt"
                onClick={() => this.delete(index)}
              ></i>
            </li>
          ))}
        </ul>
      </div>
    );
  }
}

export default TodoApp;
